import React, { useState } from "react";
import Button from './Button';
import TextField from '@mui/material/TextField';

const AddTask = (props, showAdd) => {
  const [inputText, setInputText] = useState({
    title: "",
  })

  const onChange = e => {
    setInputText({
      ...inputText,
      [e.target.name]: e.target.value,
    })
  }

  const handleSubmit = e => {
    e.preventDefault();
    if (inputText.title.trim()) {
      props.addTodoProps(inputText.title);
      setInputText({
        title: "",
      })
    } else {
      alert("Voer eerst een taak in")
    }
  };

  return (
    <form onSubmit={handleSubmit} className="form-container">
      <div className="input-text">
        <TextField id="outlined-basic" label="Taak" variant="outlined" type="text" autoFocus className="input-text" placeholder="Voer hier een taak in" value={inputText.title} name="title" onChange={onChange} />
        <Button />
      </div>
    </form>
  )
}

export default AddTask
